<script>
export default {
    props: [
        'title',
        'review',
        'image',
        'price',
        'url'
    ],
    data() {
        return {
            sizeActive: '',
            totalSizes: [
                {'size': 6},
                {'size': 6.5},
                {'size': 7},
                {'size': 7.5}
            ],
            love: false,
            shoesLink: [
                {
                    'title': 'Sneakers 2021',
                    'url': 'nike-sneakers-edition',
                    'price': 180,
                    'image': require(`@/assets/images/shoes/shoe1.webp`),
                    'review': 4.5,
                },
                {
                    'title': 'Boots Mercurial',
                    'url': 'nike-boots-mercurial',
                    'price': 210,
                    'image': require(`@/assets/images/shoes/shoe2.webp`),
