                    'price': 145,
                    'image': require(`@/assets/images/shoes/shoe4.webp`),
                    'review': 4.1,
                }
            ]
        }
    },
    methods: {
        toggleActive(size){
            let audio = new Audio(require(`@/assets/sound/popup.mp3`));
            audio.play();
            return this.sizeActive = String(size);
        },
        heart(){
            this.love = !this.love;
            if(this.love){
                let audio = new Audio(require(`@/assets/sound/popup.mp3`));
                audio.play();
            }
        },
        sound(){
            let audio = new Audio(require(`@/assets/sound/popup.mp3`));
            audio.play();
        }
    },
    mounted() {
        gsap.from('.image__shoe', {opacity: 0, duration: 5, delay: 1, x:100 })
        gsap.from('.background__shoe', {opacity: 0, duration: 2, delay: .5, x:200 })
        gsap.from('.shoe-options', {opacity: 0, duration: 3, delay: .5, x:-200 })
        gsap.from('.shoe-name', {opacity: 0, duration: 3, delay: .3, x:100 })
        gsap.from('.calification', {opacity: 0, duration: 5, delay: .3, x:50 })
        
    },
}
</script>

