import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue';
import Shoe from '../views/Shoe.vue';


Vue.use(VueRouter)

const routes = [
  { path: '/', 
    component: Home
  },
  { path: '/:url', 
    name: 'viewToProduct',
    component: Shoe, 
    props: true
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
